                          <input type="text" v-model="date_of_birth" placeholder="Напишите">
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
          </div>

          <div class="row">
            <div class="col-xl-4">
              <div class="res_title">
                Образование
              </div>
            </div>

            <div class="col-xl-8">
              <div class="row">

