                  <div class="resume_input">
                    <input type="text" v-model="class_grade" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Закончил школу</label>
                  <div class="resume_input">
                    <input type="date"  placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Закончил год</label>
                  <div class="resume_input">
                    <input type="date" placeholder="Напишите">
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-xl-4">
              <div class="res_title">
                Уровень знания языка
              </div>
            </div>
            <div class="col-xl-8">
              <div class="row">
                <div v-for="(item,ind) in language" :key="ind">
                  <div class="row child_lang">
                    <div class="col-xl-6">
                      <label  class="resume_label">Язык</label>
                      <div class="resume_input">
                        <input type="text" v-model="item.lang_name" placeholder="Напишите">
                      </div>
                    </div>
                    <div class="col-xl-6">
                      <label  class="resume_label">Уровень </label>
                      <div class="resume_input">
                        <input type="text" v-model="item.lang_dagre.text" placeholder="Напишите">
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Язык</label>
                  <div class="resume_input">
                    <input type="text" v-model="lang_name" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Уровень</label>
                  <div class="resume_input">
                    <div class="resume_select">
                      <model-select
                          :options="options"
                          v-model="item"
                          placeholder="Выберите"
                          @input="onSelect()"
                      >
                      </model-select>
                    </div>
                  </div>
                </div>
                <div class="col-xl-12">
                  <div class="add_language">
                    <a @click="add_input()">
                      <div>
                        Добавить другой язык
                      </div>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-xl-4">
              <div class="res_title">
                Рабочий стаж
              </div>
            </div>
            <div class="col-xl-8">
              <div class="row">
                <div class="col-xl-6">
                  <label  class="resume_label">Название места работы</label>
                  <div class="resume_input">
                    <input type="text" v-model="work_experiences" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">В какой должности</label>
                  <div class="resume_input">
                    <input type="text" v-model="expected_position"  placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">expected_salary</label>
                  <div class="resume_input">
                    <input type="text" v-model="expected_salary"  placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Дата начала</label>
                  <div class="resume_input">
                    <input type="date" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Дата окончания срока</label>
                  <div class="resume_input">
                    <input type="date" placeholder="Напишите">
                  </div>
                </div>

                <div class="col-xl-12">
                  <div class="add_language">
                    <a @click="addJobs()">
