            <div class="col-xl-8">
              <div class="row">
                <div class="col-xl-12">
                  <label  class="resume_label">Ваши интересы</label>
                  <div class="resume_input">
                    <input type="text" v-model="interests" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Ваши интересы</label>
                  <div class="resume_input">
                    <input type="text" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Ожидаемая заработная плата</label>
