              </div>
            </div>
            <div class="col-xl-8">
              <div class="row">
                <div class="col-xl-6">
                  <label  class="resume_label">Сертификат</label>
                  <div class="resume_input">
                    <input type="text" placeholder="Напишите">
                  </div>
                </div>
                <div class="col-xl-6">
                  <label  class="resume_label">Название сертификата</label>
                  <div class="resume_input">
                    <input type="text" placeholder="Напишите">
                  </div>
                </div>

                <div class="col-xl-12">
                  <div class="add_language">
                    <a @click="onSelect()">
                      <div>
                        Добавить другой сертификат
                      </div>
                    </a>
                  </div>
                </div>
                <div class="col-xl-12">
